<template>
    <main class="content margin padding">
        <header>Projects</header>
        <v-list></v-list>
    </main>
</template>

<script>
    import ProjectResource from 'resources/project';
    import ListComponent from 'components/list/list';

    module.exports = {

        data() {
            return {
                projects: [],
            };
        },

        /**
         * @type {Object}
         */
        route: {

            /**
             * @type {Boolean}
             */
            waitForData: true,

            /**
             * Fetch route data
             *
             * @param  {Object}     transition
             * @return {Promise}
             */
            data(transition) {
                return this.$resources({
                    projects: ProjectResource.get(this.$route.query),
                });
            },
        },

        /**
         * @type {Object}
         */
        components: {
            'v-list': ListComponent,
        },
    };
</script>



